<template>
  <div>
    <div style="margin-bottom: 5px">
      <el-input
        placeholder="请输入名字"
        suffix-icon="el-icon-search"
        v-model="name"
        style="width: 200px"
        @keyup.enter.native="loadPost"
      >
      </el-input>
      <el-select
        v-model="sex"
        placeholder="请选择性别"
        style="width: 200px; margin-left: 10px"
      >
        <el-option
          v-for="item in sexOptation"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-button type="primary" style="margin-left: 10px" @click="loadPost"
        >查询</el-button
      >
      <el-button type="info" style="margin-left: 10px" @click="clear"
        >重置</el-button
      >

      <el-button
        type="primary"
        icon="el-icon-plus"
        @click="dialogVisible = true"
        >添加</el-button
      >

      <el-dialog
        title="添加信息"
        :visible.sync="dialogVisible"
        width="500px"
        :before-close="handleClose"
      >
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="账号">
            <el-col :span="20">
              <el-input v-model="form.no"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="姓名">
            <el-col :span="20">
              <el-input v-model="form.name"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="密码">
            <el-col :span="20">
              <el-input v-model="form.password"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="年龄">
            <el-col :span="20">
              <el-input v-model="form.age"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="form.sex">
              <el-radio label="0">女</el-radio>
              <el-radio label="1">男</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="电话">
            <el-col :span="20">
              <el-input v-model="form.phone"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="角色">
            <el-select v-model="form.roleId" placeholder="请选择用户角色">
              <el-option label="管理员" value="1"></el-option>
              <el-option label="用户" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="add()">确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <el-table
      :data="tableData"
      border
      style="width: 100%; font-size: 15px"
      :header-cell-style="{ 'background-color': '#909399', color: '#303133' }"
    >
      <el-table-column prop="id" label="ID" min-width="10%"> </el-table-column>
      <el-table-column prop="no" label="账号" min-width="10%">
      </el-table-column>
      <el-table-column prop="name" label="姓名" min-width="15%">
      </el-table-column>
      <el-table-column prop="age" label="年龄" min-width="10%">
      </el-table-column>
      <el-table-column prop="sex" label="性别" min-width="10%">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.sex === 1 ? 'primary' : 'success'"
            disable-transitions
            style="font-size: 15px"
            >{{ scope.row.sex === 1 ? "男" : "女" }}</el-tag
          >
        </template>
      </el-table-column>
      <el-table-column prop="roleId" label="角色" min-width="15%">
        <template slot-scope="scope">
          <el-tag
            :type="
              scope.row.roleId === 0
                ? 'danger'
                : scope.row.roleId === 1
                ? 'warning'
                : 'success'
            "
            disable-transitions
            style="font-size: 15px"
            >{{
              scope.row.roleId === 0
                ? "超级管理员"
                : scope.row.roleId === 1
                ? "管理员"
                : "普通用户"
            }}</el-tag
          >
        </template>
      </el-table-column>
      <el-table-column
        prop="phone"
        label="电话"
        show-overflow-tooltip
        min-width="20%"
      >
      </el-table-column>
      <el-table-column label="操作" min-width="20%">
        <template slot-scope="scope">
          <el-button type="primary" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >
          <el-button
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[2, 5, 7, 10, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="font-size: 15px"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      pageNum: 1,
      pageSize: 7,
      total: 0,
      name: "",
      sex: "",
      sexOptation: [
        {
          value: "0",
          label: "女",
        },
        {
          value: "1",
          label: "男",
        },
      ],
      dialogVisible: false,
      form: {
        no: "",
        name: "",
        password: "",
        age: "",
        sex: "",
        phone: "",
        roleId: "",
      },
    };
  },
  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    loadPost() {
      this.$axios
        .post(this.$httpUrl + "/user/listPage", {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          param: {
            name: this.name,
            sex: this.sex,
          },
        })
        .then((res) => res.data)
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            this.tableData = res.data;
            this.total = res.total;
          } else {
            alert("获取数据失败");
          }
        });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.loadPost();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pageNum = val;
      this.loadPost();
    },
    clear() {
      this.name = null;
      this.sex = "";
      this.loadPost();
    },
    add() {
      this.$axios
        .post(this.$httpUrl + "/user/save", this.form)
        .then((res => res.data))
        .then(res => {
          if(res == true) {
            alert("添加成功");
            this.dialogVisible = false;
            this.loadPost()

          }else{
            alert("添加失败")
          }
        });
    },
  },
  beforeMount() {
    this.loadPost();
  },
};
</script>

<style  lang="scss" scoped>
</style>